.hero_slider {
    width: 100%;
    display: flex;
    justify-content: center;

    .container {
        width: calc(100% - 0px);
        display: flex;
        justify-content: center;
        padding: 0px;
        max-width: initial;

    }

    .slide {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        position: relative;
        background-size: contain;
        background-position: center bottom;
        background-repeat: no-repeat;
        padding-top: 100px;
        height: 120dvh;

        @media (max-width: 768px) {
            height: 70dvh;
        }

        @media (min-width: 1500px) {
            height: 130dvh;
        }

        .slide-content {
            width: calc(100% - 400px);
            flex-direction: column;
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 1;
            gap: 24px;

           

            @media (max-width: 768px) {
                width: calc(100% - 40px);
            }

            @media (min-width: 1500px) {
                width: calc(100% - 40%);
            }

            .title {
                color: #602BCA;
                text-align: center;
                font-family: Poppins, sans-serif;
                font-size: 24px;
                font-style: normal;
                font-weight: 400;
                line-height: 124%;
                /* 29.76px */
                letter-spacing: -0.48px;

                @media (max-width: 768px) {
                    text-align: center;
                    /* title-xl */
                    font-size: 23px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 136%;
                    /* 31.28px */
                    letter-spacing: -0.46px;
                }
            }

            .sub-title {
                text-align: center;
                /* H2/H2 Large */
                font-family: Poppins, sans-serif;
                font-size: 52px;
                font-style: normal;
                font-weight: 400;
                line-height: 124%;
                /* 64.48px */
                letter-spacing: -1.04px;
                background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
                background-clip: text;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;

                @media (max-width: 768px) {
                    font-size: 35px;
                }
            }

            .text {
                p {
                    color: #4F4B58;
                    text-align: center;
                    font-family: Figtree, sans-serif;
                    font-size: 24px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 24px;
                    /* 100% */
                    letter-spacing: -0.24px;

                    @media (max-width: 768px) {
                        font-size: 18px;
                    }
                }
            }


        }

        .slide-image {
            width: 100%;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                opacity: 0;
            }
        }
    }

    .swiper-pagination {
        bottom: 36px;

        .swiper-pagination-bullet {
            opacity: 1;
            width: 8px;
            height: 8px;
            margin: 0 12px;
            background-color: rgba(255, 255, 255, 0.64);
        }

        .swiper-pagination-bullet-active {
            width: 10px;
            height: 10px;
        }
    }
}